export const cartItemCount = (state) => {
    return state.cart.length;
}
export const userCount = (state) => {
    return state.user.length;
}

export const restuarantId = (state) => {
    return state.cart[0].restuarant;
}

export const cart = (state) => {
    return state.cart;
}


export const notification = (state) => {
    return state.notification;
}

export const orderCartCount = (state) => {
    return state.ordercart.length;
}

export const cartItemTotal = (state) => {
    let total = 0;

    state.cart.forEach(element => {
        total += element.total
    });
    return total;
}

export const orderCartTotal = (state) => {
    let total = 0;

    state.ordercart.forEach(element => {
        total += element.total
    });
    return total;
}