<script>

import axios from 'axios';


export default {
    name: 'Foodlist',

    data(){
  return{
  items:[],
  quantity: this.quantity,
    id: this.$route.params.id,
    category: this.$route.params.category,
  }
    },

    methods: {
    addToCart (name, base, addition, id, packed, packamount) {

        if (packed == 0){

            if (!this.quantity){
            this.quantity = 0;
             }
        this.$store.dispatch('addProductToCart', {
            name: name,
            base: base,
            quantity: this.quantity,
            total: base + (addition * this.quantity),
            restuarant: this.id,
            itemId: id,
            addition: addition,
        })
        this.quantity = "";

        }

        else{


        if (!this.quantity){
            this.quantity = 0;
             }
        this.$store.dispatch('addProductToCart', {
            name: 'Pack',
            base: packamount,
            quantity: 0,
            total: packamount,
            restuarant: this.id,
            itemId: 0,
            addition: 0,
        })
        this.quantity = "";
        

            if (!this.quantity){
            this.quantity = 0;
             }
        this.$store.dispatch('addProductToCart', {
            name: name,
            base: base,
            quantity: this.quantity,
            total: base + (addition * this.quantity),
            restuarant: this.id,
            itemId: id,
            addition: addition,
        })
        this.quantity = "";
        }


        






    },
    getID: function(id){
    alert(id);
    }
    },

mounted(){
axios.get('https://mobile-waiter12345.herokuapp.com/v1/restaurants/' +this.id + '?loadOrders=yes&loadItems=yes&category=' + this.category)
.then(response => (this.items = response.data.data.response))
}
}

</script>

